.App 
{
  display: flex;
  justify-content: center; 
  align-items: center;
  width: 100vw;
  height: 100vh; 
}

label 
{
  font-weight: bold;
  display: flex;
  margin-bottom: 5px;
}

input + label 
{
  font-weight: bold;
  display: inline-flex;
  margin-right: 20px;
}

input[type='text'],
input[type='email'],
textarea,
select
{
  display: block;
  width: 400px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-control {
  margin-bottom: 20px;
}

.error {
  color: red; 
  margin: 0;
  padding: 0;
}
